import React from 'react'
import Users from './Users'
import preloader from './../../assets/images/loading-9.gif'


class UsersClass extends React.Component {
  

  componentDidMount = () => {
   this.props.getUsersThunk(this.props.current, this.props.pageSize);
  }
   
  pageChanger = (current) => {
   this.props.pageChangerThunk(current, this.props.pageSize)

  }
  
  render () {   
    
    return <>
    {this.props.isFetching ? <img  src={preloader}/> : null}
    <Users total={this.props.total} pageSize={this.props.pageSize} portionSize={this.props.portionSize} current={this.props.current} pageChanger={this.pageChanger} users={this.props.users} unfollowThunk={this.props.unfollowThunk} followThunk={this.props.followThunk} followingInProgress={this.props.followingInProgress}  />
  </>
  }
}



export default UsersClass;