import { usersAPI } from "../API/api";
import { itemInArray } from "../utils/helper/follow/itemInArray";

const followC = 'follow';
const unfollowC = 'unfollow';
const setUsersC = 'setUsers';
const setPageC = 'setPage';
const setTotalC = 'setTotal';
const preloaderC = 'preloader';
const followingProgressC = "followingProgressConst";

let initialState = {
    users : [],
    pageSize: 5,
    portionSize: 10,
    total: 0,
    current: 1,
    isFetching: true,
    followingInProgress: [] 
}

const UsersReducer = (state=initialState,action) => {
    switch (action.type) {
        case followC:
            return {
                ...state,
                users: itemInArray(state.users, "id", action.userID, {followed: true})
            }
        case unfollowC: 
        return {
            ...state,
            users:  itemInArray(state.users, "id", action.userID, {followed: false})
        } 

        case setUsersC: {
            return {...state, users: action.users}
        }

        case setPageC: {
            return {...state, current: action.current}
        }
        
        case setTotalC: {
            return {...state, total: action.total}
        }

        case preloaderC: {
            return {...state, isFetching: action.isFetching}
        }

        case followingProgressC: {
            return {...state, followingInProgress: action.smthHappening ? [...state.followingInProgress, action.userId] : state.followingInProgress.filter(id => id !== action.userId)}
        }

        default:
            return state;
    }
}

export const follow = (userID) => ({type: followC, userID})
export const unfollow = (userID) => ({type: unfollowC, userID})
export const setUsers = (users) => ({type: setUsersC, users})
export const setPage = (current) => ({type: setPageC, current})
export const setTotal = (totalUsers) => ({type: setTotalC, total: totalUsers})
export const preloader = (isFetching) => ({type: preloaderC, isFetching})
export const followingProgress = (smthHappening, userId) => ({type: followingProgressC, smthHappening, userId})

export const getUsersThunk = (current, pageSize) => { 
   return (dispatch) => {
    dispatch(preloader(true));
    dispatch(setPage(current));
    usersAPI.getUsers(current, pageSize).then(data =>{
    dispatch(preloader(false));
    dispatch(setUsers(data.items));
    dispatch(setTotal(data.totalCount));
  })
}
}

export const pageChangerThunk = (current, pageSize) => {
    return (dispatch) => {
        dispatch(setPage(current));
        dispatch(preloader(true));
        usersAPI.getUsers(current, pageSize).then(data =>{
          dispatch(preloader(false));
          dispatch(setUsers(data.items));
    })
}
}

const followingPattern = (id,request,actionCreator, dispatch) => {
    dispatch(followingProgress(true, id));
    request(id).then(response => {
      if (response.data.resultCode === 0) {
            dispatch(actionCreator(id))
        }
      dispatch(followingProgress(false, id));
     })
    }

export const unfollowThunk = (id) => {
    return (dispatch) => {
    followingPattern(id, usersAPI.unfollow.bind(usersAPI), unfollow, dispatch)
    }
}

export const followThunk = (id) => {
    return (dispatch) => {
    followingPattern(id, usersAPI.follow.bind(usersAPI), follow, dispatch)
    }
}



export default UsersReducer;