import s from './ProfileInfo.module.css'
import photouser from './../../../assets/images/loading-9.gif'
import yes from './../../../assets/images/check-yes-ok-icon-10.png'
import ProfileStatus from './ProfileStatus'
import ProfileStatusWithHooks from './ProfileStatusWithHooks'

const ProfileInfo = (props) => {

if (!props.profile) {
  return <img src={photouser} />
}


return (
    <div>
    <div className={s.cosmos}>
      <img src='https://naked-science.ru/wp-content/uploads/2018/04/field_image_istock-516189065.jpg' />
      </div>
<div className={s.description}>
        <div>{props.profile.fullName}</div>
        <img src={props.profile.photos.large}/>
        <div><ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/></div>
        <div className={s.status}>{props.profile.aboutMe}</div>
        <div>
         <div className={s.text}>Looking for a job</div>
          {props.profile.lookingForAJob ? <div className={s.yes}><img src={yes}/></div> : <></>}
        </div>
      </div>
      </div>
)
}

export default ProfileInfo;