import React from 'react'
import { connect } from 'react-redux';
import { setUserProfileThunk, getStatusThunk, updateStatusThunk } from '../../Redux/profileReducer';
import Profile from './Profile';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import AuthRedirect  from '../../hoc/AuthRedirect';
import { compose } from 'redux';
import Login from '../Login/login';





class ProfileContainer extends React.Component {


  componentDidMount() {
    let userId = this.props.router.params.userId
    if (!userId) {
      userId = this.props.userId;
    } 
    this.props.setUserProfileThunk(userId)
    this.props.getStatusThunk(userId)
  }

  
   
  render () {
    if (!this.props.userId && !this.props.router.params.userId) {
      return <Login/>
    }
    return (
      <div>
      <Profile {...this.props} profile={this.props.profile} status={this.props.status} updateStatus={this.props.updateStatusThunk}/>
      </div>
    )
  }
    
}



let mapStateToProps = (state) => ({
  profile: state.profilePage.profile,
  status: state.profilePage.status,
  userId: state.auth.id,
  isAuth: state.auth.isAuth
});

function withRouter(Component) {
  function ComponentWithRouterProp(props) {
    let location = useLocation();
    let navigate = useNavigate();
    let params = useParams();
    return(
      <Component
         {...props}
         router={{location, navigate, params}}
         />
    );
  }
   
  return ComponentWithRouterProp;

}

export default compose(connect(mapStateToProps, {setUserProfileThunk, getStatusThunk, updateStatusThunk}), withRouter)(ProfileContainer)