const addMessage = 'addMessage';
const updateMessageConst = 'updateMessage';

let initialState = {
  messages: [
    {id: 1, message: 'Hellloooo', color: 'https://htmlcolorcodes.com/assets/images/colors/pastel-orange-color-solid-background-1920x1080.png'},
    {id: 2, message: 'Absolutely not', color: 'https://i.pinimg.com/236x/0b/93/bf/0b93bfa28265968dc52569731d4eb1b0.jpg'},
    {id: 3, message: 'Not again..', color: 'https://i.pinimg.com/236x/0b/93/bf/0b93bfa28265968dc52569731d4eb1b0.jpg'},
    {id: 4, message: 'Where have all the kangaroos gone?!', color: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAOEAAADhCAMAAAAJbSJIAAAAA1BMVEW77LRQtudeAAAASElEQVR4nO3BgQAAAADDoPlTX+AIVQEAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAADwDcaiAAFXD1ujAAAAAElFTkSuQmCC'}
  ],

dialogs: [
  {id: 1, name: 'Michael', image:'https://i.pinimg.com/originals/4c/ac/d0/4cacd03e19d8b8ab7b939462176f8355.png'},
  {id: 2, name: 'Dwight', image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT7k41iIlVjyzN3DVXLf0rFJP3uz_3aQ1pxdVncx9Mlfn8srso6FiR-rmhZ5OGHUJr9tpQ&usqp=CAU'},
  {id: 3, name: 'Jim', image: 'https://www.personality-database.com/profile_images/15.png'}
]
}

const dialogsReducer = (state=initialState, action) => {
    switch (action.type) {
        case addMessage: {
            let newMessage = {
                id: 5,
                message: action.formData,
                color: 'https://i.pinimg.com/236x/0b/93/bf/0b93bfa28265968dc52569731d4eb1b0.jpg'
              };
              let stateCopy = {...state}
              stateCopy.messages = [...state.messages];
              stateCopy.messages.push(newMessage);
              stateCopy.newMessage = '';
              return stateCopy;
            }
            default:
                return state;

    }
   
   }

   export const addDM = (formData) => {
    return {
    type: addMessage,
    formData
  }
  }
  
  export const updateMessage = (dmm) => {
    return {
    type: updateMessageConst,
    dm: dmm
  }
  }

   export default dialogsReducer;