import { Paginator } from '../../utils/paginator';
import { UserOne } from './UserOne';


let Users = (props) => {
    
    return (
      <div>
        <div>
         <Paginator total={props.total} pageSize={props.pageSize} current={props.current} pageChanger={props.pageChanger} portionSize={props.portionSize}/>
        </div> 
        {props.users.map(u =><UserOne user={u} key={u.id} followingInProgress={props.followingInProgress} unfollowThunk={props.unfollowThunk} followThunk={props.followThunk}/>)}
       </div>
     
      )
  }


export default Users;