import { ProfileAPI, usersAPI } from "../API/api";

const addPost = 'addPost';
const updatePostConst = 'updatePost';
const set_User_Profile = 'set_User_Profile'
const setStatusC = 'setStatus'

let initialState = {
  posts: [
    {id: 1, message: 'nebo polno bezzabotnoy negi segondya', number: 17},
    {id: 2, message: 'beznogm', number: 3},
    {id: 2, message: 'letnie mechty', number: 8},
    {id: 2, message: 'ni ya ne videl ni ty', number: 2}
  ],
  profile: null,
  status: ''
}

const profileReducer = (state=initialState, action) => {
    switch(action.type) {
        case addPost: {
            let newPost = {
                id: 5,
                message: action.formData,
                number: 0
              };
              let stateCopy = {...state};
              stateCopy.posts = [...state.posts]
              stateCopy.posts.push(newPost);
              stateCopy.newPostText = '';
              return stateCopy;
            }
        case set_User_Profile: {
          return {...state, profile: action.profile}
        }
        case setStatusC: {
          return {...state, status: action.status}
        }
        default:
            return state;

    }
    
}

export const addPostType = (formData) => {
    return {
    type: addPost,
    formData: formData
  }
  }
  
  export const updatePost = (text) => {
    return {
    type: updatePostConst,
    newText: text
  }
  }

  export const setUserProfile = (profile) => {
    return {
      type: set_User_Profile,
      profile
    }
  }

  export const setStatus = (status) => {
    return {
      type: setStatusC,
      status
    }
  }

  export const setUserProfileThunk = (userId) => {
    return (dispatch) => {
      usersAPI.userSmth(userId).then(response =>{
      dispatch(setUserProfile(response.data));
  })
    }
  }

  export const getStatusThunk = (userId) => {
    return (dispatch) => {
      ProfileAPI.getStatus(userId).then(response => {
        dispatch(setStatus(response.data))
      })
    }
  }


  export const updateStatusThunk = (status) => {
    return (dispatch) => {
      ProfileAPI.updateStatus(status).then(response => {
        if (response.data.resultCode === 0)
        dispatch(setStatus(status))
      })
    }
  }

export default profileReducer;