import { useState } from 'react';
import s from './paginator.module.css'

export const Paginator = (props) => {

let pagesCount = Math.ceil(props.total/props.pageSize);

let pages = [];

for(let i=1; i<=pagesCount; i++) {
    pages.push(i);
}
let  portionCount = Math.ceil(pagesCount/props.portionSize)
let [portionNumber, setPortionNumber] = useState(1)
let leftNumber = (portionNumber * props.portionSize) - (props.portionSize - 1) 
let rightNumber = portionNumber * props.portionSize


return (
<div>
    {portionNumber > 1 && <button onClick={()=>{setPortionNumber(portionNumber - 1)}}>PREV</button>}
{pages.filter(p => p >= leftNumber && p <= rightNumber).map((p) => {
return <span className={props.current === p && s.selectedpage} onClick={()=>{props.pageChanger(p)}}>{p}</span> })}

   {portionNumber < portionCount && <button onClick={()=>{setPortionNumber(portionNumber + 1)}}>NEXT</button>}
</div>
)
}

// {pages.map(p => { return <span className={props.current === p && s.selectedpage} onClick={()=>{props.pageChanger(p)}}>{p}</span> })} 