import { connect } from "react-redux"
import { Navigate } from "react-router-dom"
import { Field } from "redux-form"
import { reduxForm } from "redux-form"
import { loginThunk } from "../../Redux/authReducer"
import { required } from "../../utils/validators/validators"
import { Input } from "../common/forms/form1"
import s from "../common/forms/form1.module.css"

const LoginForm = (props) => {
    return  <form onSubmit={props.handleSubmit}>
    <div>
        <Field placeholder={'Login'} component={Input} name={'login'} validate={[required]}/>
    </div>
    <div>
    <Field placeholder={'Password'} component={Input} name={'password'} type={'password'} validate={[required]} />
    </div>
    <div>
    <Field type={'checkbox'} component={'input'} name={'rememberme'}/> remember me
    </div>
    
        {props.error && <div className={s.loginerror}>
        {props.error}
        </div>
        }

    <div>
        <button> log in</button>
    </div>
</form>
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)

 
const Login = (props) => {
    const onSubmitFunction = (formData) => {
        props.loginThunk(formData.login, formData.password, formData.rememberme)
       }

       if (props.isAuth) {
        return <Navigate to='/profile' />
       }
  
    return (
        
        <div>
            <h1>LOGIN</h1>
            <LoginReduxForm  onSubmit={onSubmitFunction}/>
        </div>
    )
    }

    const mapStateToProps = (state) => ({
        isAuth: state.auth.isAuth
    })

export default connect(mapStateToProps, {loginThunk})(Login);