import React from 'react'
import { Field } from 'redux-form';
import { reduxForm } from 'redux-form';
import { maxLength, required } from '../../../utils/validators/validators';
import { Textarea } from '../../common/forms/form1';
import s from './MyPosts.module.css'
import Post from './Post/Post';

const maxLength10 = maxLength(10)

const PostForm = (props) => {
  
  return <form onSubmit={props.handleSubmit}>
        <div>
          <Field component={Textarea} name={'post'} validate={[required, maxLength10]}/>
        </div>
        <div>
          <button>Add Post</button>
        </div>
        </form>
}

const PostReduxForm = reduxForm({form: 'post'})(PostForm)

const MyPosts = (props) => {

  const onSubmitF = (formData) => {
      props.addPostText(formData.post);
    }
   

  let postElements = props.posts.map(post => <Post message={post.message} number={post.number} /> )

    return (
     
      <div className={s.item}>
        <h3>My posts</h3>
         
        <PostReduxForm onSubmit={onSubmitF}/>
        <div className={s.posts}>
        {postElements}
        </div>
       </div>
    )
}




export default MyPosts;