import s from './Post.module.css'

const Post = (props) => {
    return (
     <div className={s.item}>
      <img src='https://freepngimg.com/thumb/mario_bros/91876-square-art-bros-mario-allstars-super.png' />
      {props.message}
      <div className={s.like}>
      <img src='http://pixelartmaker-data-78746291193.nyc3.digitaloceanspaces.com/image/8881121e2fde0bf.png' />
       <span>{props.number} likes</span>
      </div>
     </div>

    )
}

export default Post;