import s from './UserOne.module.css'
import photouser from './../../assets/images/pic_1171831236_1.png'
import { NavLink } from 'react-router-dom'

export const UserOne = (props) => {
    const u = props.user  
return (
    <div>
      <span>
      <div className={s.ava}>
        <NavLink to={'/profile/' + u.id}>
          <img src={u.photos.small !== null ? u.photos.small : photouser}/>
          </NavLink>
      </div>

      <div>{u.name}</div>
      <div>{u.status}</div>

      <div>
         {u.followed 
         ? <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={()=>{
          props.unfollowThunk(u.id)}}>unfollow</button> 

         
         
         : <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={()=>{
          props.followThunk(u.id)
            
          }}>follow</button>}
      </div>
    </span> 
 </div>
)
}