import { stopSubmit } from "redux-form";
import { authAPI, usersAPI } from "../API/api";

const setUserDataConst = 'setUserData';

let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false
}

const authReducer = (state=initialState, action) => {
    switch(action.type) {
  case setUserDataConst: {
    return {
        ...state,
        ...action.data
    }
    }
    default:
        return state;
        
}
}

export const setUserData = (id, email, login, isAuth) => {
    return {
        type: setUserDataConst,
        data: {id, email, login, isAuth}
    }
};

export const setUserDataThunk = () => (dispatch) => {
    return usersAPI.authMe().then(response => {
            if (response.data.resultCode === 0) {
                dispatch(setUserData(response.data.data.id, response.data.data.email, response.data.data.login, true))
            }
        })
    
}


export const loginThunk = (email, password, rememberMe) => (dispatch) =>{
    
        authAPI.login(email, password, rememberMe).then(response => {
            if (response.data.resultCode === 0) {
               dispatch(setUserDataThunk());
            } else {
                let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error"
                dispatch(stopSubmit("login", {_error: message}));
            }
        })
    
}

export const logoutThunk = () => {
    return (dispatch) => {
        authAPI.logout().then(response => {
            if (response.data.resultCode === 0) {
               dispatch(setUserData(null, null, null, false))
            }
        })
    }
}

export default authReducer;