import { setUserDataThunk } from "./authReducer";

const initialC = "initialC"


let initialState = {
    initialized: false
}

const appReducer = (state=initialState, action) => {
    switch (action.type) {
        case initialC:
            return {
                ...state,
                initialized: true
            }
    
    default: 
       return state;
    }
}

export const initializedSucces = () => ({type: initialC})

export const initializeThunk = () => (dispatch) => {
      let promise = dispatch(setUserDataThunk());
      promise.then(()=>{
        dispatch(initializedSucces());
      })
}

export default appReducer;