// 
const formPattern = ({input, meta, child, element, ...props}) => {
 
    const hasError = meta.touched && meta.error

    return(
        <div className={s.form + " " + (hasError ? s.error : "")}>
            <div>
                {props.children}
            </div>
            {hasError && <span>{meta.error}</span>}
        </div>
    )

 }

 const Input1 = (props) => {
    const {input, meta, child, ...restProps} = props;
    <formPattern {...props}><input {...input} {...restProps}/></formPattern>
}