import { connect } from "react-redux";
import { setPage, preloader, getUsersThunk, pageChangerThunk, unfollowThunk, followThunk } from "../../Redux/usersReducer";
import { getPageSize, getTotal, getCurrent, getIsFetching, getFollowingInProgress, reselectversion, getPortionSize } from "../../Redux/usersSelectors";
import UsersClass from "./UsersClass";


let mapStateToProps = (state) => {
   return {
      users: reselectversion(state),
      pageSize: getPageSize(state),
      total: getTotal(state),
      current: getCurrent(state),
      isFetching: getIsFetching(state),
      followingInProgress: getFollowingInProgress(state),
      portionSize: getPortionSize(state)
   }
}



 const UsersContainer = connect(mapStateToProps, {setPage, preloader, getUsersThunk, pageChangerThunk, unfollowThunk, followThunk})(UsersClass)
 export default UsersContainer;